import {
  AUTH_LOAD,
  AUTH_LOAD_SUCCESS,
  AUTH_LOAD_FAIL
} from '../constants/authTypes'

const initialState = {
  loaded: false
}

export default function auth( state=initialState, action = {} ) {
  switch (action.type) {
  case AUTH_LOAD:
    return {
      ...state,
      loaded:true
    }
  case AUTH_LOAD_SUCCESS:
    return state
  case AUTH_LOAD_FAIL:
    return state
  default:
    return state
  }
}


